'use strict';
import React, {View, Text, Navigator} from 'qunar-react-native';
import '@qnpm/react-native-ext';
import './BasicDemo';

Ext.defaults.indexView = 'BasicDemo';
Ext.defaults.navBar = {
    isShow: true,
};

var HomeScene = React.createClass({
    render: function () {
        return (
            <View style={{flex: 1}}><Text>Home</Text></View>
        )
    }
})
var Second = React.createClass({
    render: function () {
        return (
            <Navigator
                initialRoute={{ name: 'Home', component: HomeScene }}
                renderScene={(route, navigator) => {
                    const Component = route.name === 'Home' ? HomeScene : route.component
                    if (!Component) return <View>empty</View>
                    return <Component {...route.params} navigator={navigator} ></Component>
                }}
                sceneStyle={{}}
            />
        )
    }
})
React.AppRegistry.registerComponent('Second', () => Second)